@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  background-color: #fcfcfc;
  font: 400 12px 'Montserrat', sans-serif;
  color: #000000;
}

/**************************************************************************/

.heading1 {
  font: 400 5rem 'Montserrat', sans-serif;
  color: black;
}

.heading2 {
  font: 400 3rem 'Montserrat', sans-serif;
  color: black;
}

.heading3 {
  font: 400 2rem 'Montserrat', sans-serif;
  color: black;
}

.normal-text {
  font: 400 1.75rem 'Montserrat', sans-serif;
  color: black;
}

.option-text-m {
  font: 400 1.34rem 'Montserrat', sans-serif;
  color: black;
}

.option-text-s {
  font: 400 1.17rem 'Montserrat', sans-serif;
  color: black;
}

.small-text {
  font: 400 1rem 'Montserrat', sans-serif;
  color: black;
}

.blck-border {
  border-style: solid;
  border-width: 1px;
  border-color: black;
}

/**************************************************************************/

.App {
  min-height: 100vh;
}

.page {
  display: flex;
  flex-direction: column;
  height: 100%;
  height: -webkit-fill-available;
}

.content{
  flex: 1 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

a:hover {
  background-color: #BFBFBF;
}

a:visited {
  color: #000000;
}

/**************************************************************************/

.Auth{
  display: flex;
  justify-content: center;
  min-height: 100vh;
  width: 100%;
  
}

.form-auth{
  display: flex;
  flex-direction: column;
  align-self: center;
}
input{
  height: 30px;
  border-radius: 4px;


}
.field{

  width: 200px;
  margin: 5px;
  padding-left: 5px;
  border-width: 1px;
  border-color: #BFBFBF;
}

.submit-button{
  margin: 10px;
  width: 100px;
  background-color: #78d0ff;
  margin-left: auto;
  margin-right: auto;
  border: 0;


}
/**************************************************************************/

footer {
  height: 100px;
  width: 100%;
  margin-top: 20px;
  background-color: #BFBFBF;
  bottom: 0;
  flex: 0 0 auto;
}

/**************************************************************************/

header {
  display: flex;
  position: fixed;
  height: 70px;
  width: 100%;
  background-color: black;
}

.logo {
  height: 50px;
  width: 300px;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 60px;
  background-image: url(/alt_nti/static/media/logo.114b46f6.png);
  background-size: contain;
  background-repeat: no-repeat;
}

/**************************************************************************/

.main {
  padding-top: 70px;
  background-color: #fcfcfc;
  margin-left: 20%;
  margin-right: 20%;
}

.title {
  margin-top: 40px;
}

.task {
  margin-top: 70px;
}

/**************************************************************************/

.message {
  display: flex;
  flex-direction: column;
  margin: 10px;
  padding-left: 5px;
  padding-right: 5px;
  background-color: #78d0ff;
  border-radius: 10px;
}

.content-box {}

.time-box {
  display: flex;
  margin-top: 5px;
  justify-content: flex-end;
}

/********************************************/

li {
  margin-left: 10px;
  padding: 2px;
}

ul {
  margin-left: 5px;
  list-style-type: none;
}
.points {
    display: flex;
    flex-direction: row;
    float: right;
    align-items: center;
    margin: 20px;
}

.bars {
    display: flex;
    flex-direction: column;
}

.bar {
    height: 25px;
    width: 200px;
    text-align: center;
    margin-top: 7px;
    margin-bottom: 7px;
    border-radius: 3px;
}

.circle {
    display: flex;
    height: 70px;
    width: 70px;
    text-align: center;
    align-items: center;
    margin-left: 10px;
    background-color: #f6f6f6;
    border-radius: 35px;
}
table {
    width: 100%;
}

tr:nth-child(odd) {
    background-color: #78d0ff;
}

tr:nth-child(even) {
    background-color: #96c7cc;
}

td {
    padding-top: 3px;
    padding-bottom: 3px;
}

.data {
    padding-left: 10px;
}

.result {
    text-align: center;
}

.right {
    color: black;
}

.wrong {
    color: #f80000;
}
.screen {
    display: flex;
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 1;
}

.gray-screen {
    display: flex;
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: 0.5;
}

.btn {
    height: 50px;
    width: 50px;
    margin: 0 10px;
    background-color: transparent;
    border: 0;
}

/********************************************************************************/

.not-open-btn {
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 0;
    background-image: url(/alt_nti/static/media/up-arrow.c28d9f7d.svg);
}

.not-close-btn {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: auto;
    background-image: url(/alt_nti/static/media/down-arrow.ba46c001.svg);
}

.msg-box {
    display: flex;
    position: fixed;
    flex-direction: column;
    height: 60%;
    width: 70%;
    bottom: 0;
    border-radius: 5px 5px 0 0;
    background-color: #fcfcfc;
}

.not-text {
    margin-bottom: auto;
    padding: 0 50px;
}

.not-panel-align {
    align-items: center;
    justify-content: center;
}

/*
    display: flex;
    align-items: center;
    justify-content: center;
    */

/********************************************************************************/

.open-chat-btn {
    position: fixed;
    right: 0;
    bottom: 0;
    float: right;
    background-image: url(/alt_nti/static/media/mail.e6956db2.svg);
}

.close-chat-btn {
    bottom: 0;
    width: 100%;
    height: 30px;
    background-color: #000;
    border: 0;
    color: #fcfcfc;
}

.chat-box {
    background-color: #fcfcfc;
    position: fixed;
    right: 0;
    bottom: 0;
    float: right;
    width: 250px;
    height: 80%;
    border-radius: 4px 0 0 0;
}

/********************************************************************************/

.open-menu-btn {
    display: flex;
    position: fixed;
    flex-direction: column;
    float: left;
    margin-top: 70px;
    background-image: url(/alt_nti/static/media/menu.9a39f553.svg);
}

.close-menu-btn {
    width: 100%;
    height: 50px;
    background-color: #000000;
    border: 0;
    color: #fcfcfc;
}

.nav-bar {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 300px;
    margin-top: 70px;
    background-color: #fcfcfc;
}
